import { CustomEventsHelper } from "../../blazor/custom-events-helper";
import { ClientComponentEmptyContextEvent } from "../../client-components/common/events";
import { DxTagNames } from "../../dx-tag-names";
export class CarouselSwipeStartEvent extends ClientComponentEmptyContextEvent {
    constructor() {
        super(CarouselSwipeStartEvent.eventName);
    }
}
CarouselSwipeStartEvent.eventName = DxTagNames.Carousel + ".swipe-start";
export class CarouselSwipeEndEvent extends CustomEvent {
    constructor(direction) {
        super(CarouselSwipeEndEvent.eventName, {
            detail: new CarouselSwipeEndEventContext(direction),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
CarouselSwipeEndEvent.eventName = DxTagNames.Carousel + ".swipe-end";
class CarouselSwipeEndEventContext {
    constructor(direction) {
        this.Direction = direction;
    }
}
export class CarouselWheelEvent extends CustomEvent {
    constructor(detail) {
        super(CarouselWheelEvent.eventName, {
            detail: detail,
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
CarouselWheelEvent.eventName = DxTagNames.Carousel + ".mouse-scroll";
export class CarouselShortcutEvent extends CustomEvent {
    constructor(detail) {
        super(CarouselShortcutEvent.eventName, {
            detail: detail,
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
CarouselShortcutEvent.eventName = DxTagNames.Carousel + ".shortcut";
CustomEventsHelper.register(CarouselSwipeStartEvent.eventName, x => x.detail);
CustomEventsHelper.register(CarouselSwipeEndEvent.eventName, x => x.detail);
CustomEventsHelper.register(CarouselWheelEvent.eventName, x => {
    const e = x.detail;
    return {
        "DeltaX": e.deltaX,
        "DeltaY": e.deltaY,
        "DeltaZ": e.deltaZ,
        "DeltaMode": e.deltaMode,
    };
});
CustomEventsHelper.register(CarouselShortcutEvent.eventName, x => {
    const e = x.detail;
    return {
        "Key": e.key,
        "Code": e.code,
        "CtrlKey": e.ctrlKey,
        "AltKey": e.altKey,
        "ShiftKey": e.shiftKey,
        "MetaKey": e.metaKey
    };
});
